<template>
  <div class="login-form">
      <h1></h1>
      <div class="g-form-line" >
        <span class="g-form-label"></span>
        <div class="g-form-input">
          <input type="text" 
          v-model="username" placeholder="">
        </div>
      </div>

      <div class="g-form-line" >
        <span class="g-form-label"></span>
        <div class="g-form-input">
          <input type="text" 
          v-model="password" placeholder="">
        </div>
      </div>
      <div class="g-form-line">
        <div class="g-form-btn">
          <a class="button" @click="onLogin"></a>
        </div>
      </div>
  </div>
</template>

<script>
export default {
  props: {
    'isShow': 'boolean'
  },
  data () {
    return {
      username: '',
      password: ''
    }
  },
  computed: {
    userErrors () {
      let status, errorText
      if (!/@/g.test(this.usernameModel)) {
        status = false
        errorText = '@'
      } else {
        status = true
        errorText = ''
      }
      return {
        status,
        errorText
      }
    },
    passwordErrors () {
      let status, errorText
      if (!/@/g.test(this.usernameModel)) {
        status = false
        errorText = '@'
      } else {
        status = true
        errorText = ''
      }
      return {
        status,
        errorText
      }
    }
  },
  methods: {
  }
}
</script>

<style scoped>

</style>


